.card {
  display: flex;
  margin: 20px;
  width: 65%;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  border: 1px solid grey;
  border-radius: 15px;
}

.card-body {
  flex: 1 1 auto;
  min-height: 1px;
  padding: 1.25rem
}

.card .card-body .date {
  font-size: 10px;
  float: right;
}

.card .card-body .title {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  margin-bottom: 40px;
}

.card .card-body .contact p {
  font-size: 11px;
}

.card .card-body .contact {
  margin-top: 10px;
}

.card .card-body .budget {
  margin-top: 10px;
  font-size: 17px;
}

.card .show-info-btn {
  float: right;
}

.card .card-body .period p {
  font-size: 14px;
}

.show-tenders-btn {
  margin-top: 20px;
}